@function getMonthWidth($calendar-gap, $child-width, $parent-padding) {
  @return ($count - 1) * $calendar-gap +
    $count * $child-width +
    2 * $parent-padding;
}

@function getDayOfWeek($orderNumber, $child-width, $calendar-gap) {
  @return ($child-width + $calendar-gap) * ($orderNumber - 1);
}
